// use server'
'use server';

/**
 * @fileOverview A tool to check if content adheres to Google AdSense policies.
 *
 * - googleAdsensePolicyChecker - A function that checks content against Google AdSense policies.
 * - GoogleAdsensePolicyCheckerInput - The input type for the googleAdsensePolicyChecker function.
 * - GoogleAdsensePolicyCheckerOutput - The return type for the googleAdsensePolicyChecker function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const GoogleAdsensePolicyCheckerInputSchema = z.object({
  content: z
    .string()
    .describe('The content to check against Google AdSense policies.'),
});
export type GoogleAdsensePolicyCheckerInput = z.infer<
  typeof GoogleAdsensePolicyCheckerInputSchema
>;

const GoogleAdsensePolicyCheckerOutputSchema = z.object({
  adherence: z
    .boolean()
    .describe(
      'Whether the content adheres to Google AdSense content policies.'
    ),
  reasoning: z
    .string()
    .describe(
      'The reasoning behind the adherence determination, referencing specific Google AdSense policies.'
    ),
});
export type GoogleAdsensePolicyCheckerOutput = z.infer<
  typeof GoogleAdsensePolicyCheckerOutputSchema
>;

export async function googleAdsensePolicyChecker(
  input: GoogleAdsensePolicyCheckerInput
): Promise<GoogleAdsensePolicyCheckerOutput> {
  return googleAdsensePolicyCheckerFlow(input);
}

const googleAdsensePolicyCheckerPrompt = ai.definePrompt({
  name: 'googleAdsensePolicyCheckerPrompt',
  input: {schema: GoogleAdsensePolicyCheckerInputSchema},
  output: {schema: GoogleAdsensePolicyCheckerOutputSchema},
  prompt: `You are an expert in Google AdSense content policies.

You will evaluate the provided content to determine if it adheres to Google AdSense policies. Provide a detailed reasoning for your determination, referencing specific policies where applicable.

Content: {{{content}}}

Determine if the content adheres to Google Adsense policies, and provide a detailed explanation.

Output in JSON format:
{
  "adherence": true or false,
  "reasoning": "Explanation referencing specific Google Adsense policies."
}
`,
});

const googleAdsensePolicyCheckerFlow = ai.defineFlow(
  {
    name: 'googleAdsensePolicyCheckerFlow',
    inputSchema: GoogleAdsensePolicyCheckerInputSchema,
    outputSchema: GoogleAdsensePolicyCheckerOutputSchema,
  },
  async input => {
    const {output} = await googleAdsensePolicyCheckerPrompt(input);
    return output!;
  }
);
