"use client";

import { useState } from "react";
import { useForm, type SubmitHandler } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Loader2, CheckCircle, XCircle } from "lucide-react";
import { googleAdsensePolicyChecker, type GoogleAdsensePolicyCheckerOutput } from "@/ai/flows/google-adsense-policy-checker";
import { useToast } from "@/hooks/use-toast";


const formSchema = z.object({
  content: z.string().min(50, "Content must be at least 50 characters long.").max(5000, "Content must be at most 5000 characters long."),
});

type FormData = z.infer<typeof formSchema>;

export default function FactCheckerClient() {
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<GoogleAdsensePolicyCheckerOutput | null>(null);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      content: "",
    },
  });

  const onSubmit: SubmitHandler<FormData> = async (data) => {
    setIsLoading(true);
    setResult(null);
    setError(null);

    try {
      const response = await googleAdsensePolicyChecker({ content: data.content });
      setResult(response);
      toast({
        title: "Check Complete",
        description: response.adherence ? "Content appears to adhere to policies." : "Content may not adhere to policies. See details.",
        variant: response.adherence ? "default" : "destructive",
      });
    } catch (e) {
      console.error("Error checking content:", e);
      const errorMessage = e instanceof Error ? e.message : "An unexpected error occurred.";
      setError(errorMessage);
      toast({
        title: "Error",
        description: `Failed to check content: ${errorMessage}`,
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
          <FormField
            control={form.control}
            name="content"
            render={({ field }) => (
              <FormItem>
                <FormLabel htmlFor="content-textarea" className="font-semibold text-lg font-body">Content to Check</FormLabel>
                <FormControl>
                  <Textarea
                    id="content-textarea"
                    placeholder="Paste your content here (50-5000 characters)..."
                    className="min-h-[200px] text-base"
                    {...field}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <Button type="submit" disabled={isLoading} className="w-full bg-primary hover:bg-primary/90 text-primary-foreground text-lg py-3">
            {isLoading ? (
              <>
                <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                Checking...
              </>
            ) : (
              "Check Content Policy"
            )}
          </Button>
        </form>
      </Form>

      {error && (
        <Alert variant="destructive">
          <XCircle className="h-4 w-4" />
          <AlertTitle>Error</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {result && (
        <Card className="mt-6 shadow-md">
          <CardHeader>
            <CardTitle className="text-2xl font-headline flex items-center">
              {result.adherence ? (
                <CheckCircle className="mr-2 h-7 w-7 text-green-500" />
              ) : (
                <XCircle className="mr-2 h-7 w-7 text-red-500" />
              )}
              Compliance Result
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-3 font-body">
            <p className={`text-lg font-semibold ${result.adherence ? "text-green-600" : "text-red-600"}`}>
              Adherence: {result.adherence ? "Compliant" : "Potentially Non-Compliant"}
            </p>
            <div>
              <h4 className="font-semibold text-md mb-1">Reasoning:</h4>
              <p className="text-base text-muted-foreground bg-secondary p-3 rounded-md whitespace-pre-wrap">{result.reasoning}</p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
